import React, { forwardRef } from 'react';
import {Card,Button} from 'react-bootstrap';
import {ManageCSRosters, updateCSstatus} from '../../../actions/call_log_actions';
import {capitalize_first_letter, title_case} from '../../../helpers/common_helper';
import {dd_value} from '../../../actions/common_actions';
import { Link} from 'react-router-dom';
import _ from 'lodash';
import FlowContainer from '../../common/container/core/FlowContainer';
import CommonReactTable from '../../../helpers/commonReact_table';
import Toggle from '../../common/component/Toggle';


class CSRosters_container extends FlowContainer{


    state ={
        columns: [],
        lists: [],
        copy: [],
        resourse : null,
        cs_status : null     

    }

    componentWillMount(){
        this.get_cs_devices()
    }

    get_cs_devices = () =>{

        ManageCSRosters(this.req({resourse : "cs_devices"}))
        .then((response) => {
            if (!response) { return };
            if (response.status === "success") {
                this.setState(state => state.columns = this.columndatas(response.data.headers))
                if (response.data.records_arr) {
                    this.setState({row_datas: response.data.records_arr, copy: response.data.records_arr })
                    
                }
            }
        })
    }

    handlestatus = (e, row) => {
        let cs_status;
        if(row.status == 'enabled'){
           cs_status = 'disabled'
        }else{
            cs_status = 'enabled'
        }
        if(window.confirm("Are you sure want to change the status of the Customer Success ?")){
            updateCSstatus(this.req({status : cs_status , cs_number : row.number}))
            .then((response) => {
                if (!response) { return }
                if (response.status === "success") {
                    
                    window.location.reload();
                }else{
                    alert(response.message + "\n" + JSON.stringify(response.data));  
                
                }
            })    
        }
    
    }

     
    columndatas(data) {
        
        return [
            {
                
                Header: <p style={{fontSize:"15px"}}>{data.person_name}</p>,
                id: "name",
                accessor: row => capitalize_first_letter(row.name),
                minWidth:150,
                maxWidth:375  
            },
            {
                Header: <p style={{fontSize:"15px"}}>{data.type}</p>,
                id: "type",
                accessor: row => capitalize_first_letter(row.type),
                minWidth:75,
                maxWidth:200
            },
            {
                Header: <p  style={{fontSize:"15px"}}>{data.number}</p>,
                id: "number",
                accessor: row => row.number,
                minWidth:170,
                maxWidth:375
            },{
                Header: <p  style={{fontSize:"15px"}}>{data.status}</p>,
                id: "status",
                accessor: row => row.status == 'enabled' ? 'Assigned' : 'Unassigned',
                minWidth:75,
                maxWidth:200
            },
            {
                Header: <p  style={{fontSize:"13px"}}>Action</p>,
                id: "cs_status",
                accessor : row=>(<Button variant = {row.status == 'enabled' ? 'danger' : 'success' } size="sm" onClick={e => this.handlestatus(e ,row) }>{row.status == 'enabled' ? 'Unassign' : 'assign'}</Button> ),
                minWidth:75,
                maxWidth:200
            },
        ]}

    render(){
        return(
            
            <>
                <div className='cs_table'>
                    <h5 style={{ color: "white" }}>{this.props.title}</h5>
                </div>

                <div className="card-body m-auto" style={{width: '850px'}}>
                    <CommonReactTable showPag={true} defaultPageSize={15} minRow={0} row_data={this.state.copy !== undefined ? this.state.copy : []} column_data={this.state.columns} resizable={false} defaultSorted={[{ id: "name", asc: true }]} />
                </div>
            </>

            
        )
    }
}
export default CSRosters_container;